import React from 'react'
import moment from 'moment';
import Moment from 'react-moment';

import TextField from '@material-ui/core/TextField';
import { RadioGroup, RadioButton } from 'react-radio-buttons'
import Table from 'rc-table';

import Layout from '../components/layout'

class IndexPage extends React.Component {
  static displayName = 'TestComponent';

  styles = ({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      width: 200,
    },
    table: {
      background: '#f3f3f3',
    },
  });

  state = { time: moment().add(7, "hours").format('YYYY-MM-DD HH:mm:ss'), base: 'sgn' };
  
  render() {

    const columns = [{
      title: 'Chặng', dataIndex: 'chang', key: 'chang',
    }, {
      title: 'Briefing (GMT +7)', dataIndex: 'briefing', key: 'briefing',
    }];

    let data = [];
    let notice = "";

    if (this.state.base === 'sgn') {
      data = [
        {
          chang: 'Nội địa',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 20 }}>{this.state.time}</Moment>,
          key: '1'
        },
        {
          chang: 'Ngắn quốc tế',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 35 }}>{this.state.time}</Moment>,
          key: '2'
        },
        {
          chang: 'Đường trung',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 50 }}>{this.state.time}</Moment>,
          key: '3'
        },
        {
          chang: 'Đường dài',
          briefing: <Moment format="HH:mm" subtract={{ hours: 2, minutes: 5 }}>{this.state.time}</Moment>,
          key: '4'
        },
      ];
      notice = "Lưu ý: Nội địa có mặt trước 1h20p, Ngắn quốc tế có mặt trước 1h35p, đường Trung có mặt trước 1h50p và đường Dài có mặt trước 2h05p";
    } else if (this.state.base === 'han') {
      let noidia, ngan = "";

      if (moment(this.state.time).format('HH') < 8) {
        noidia = <Moment format="HH:mm" subtract={{ hours: 2, minutes: 0 }}>{this.state.time}</Moment>
        ngan = <Moment format="HH:mm" subtract={{ hours: 2, minutes: 0 }}>{this.state.time}</Moment>
      } else {
        noidia = <Moment format="HH:mm" subtract={{ hours: 2, minutes: 10 }}>{this.state.time}</Moment>
        ngan = <Moment format="HH:mm" subtract={{ hours: 2, minutes: 10 }}>{this.state.time}</Moment>
      }
      data = [
        {
          chang: 'Nội địa',
          briefing: noidia,
          key: '1'
        },
        {
          chang: 'Ngắn quốc tế',
          briefing: ngan,
          key: '2'
        },
        {
          chang: 'Đường trung',
          briefing: <Moment format="HH:mm" subtract={{ hours: 2, minutes: 30 }}>{this.state.time}</Moment>,
          key: '3'
        },
        {
          chang: 'Đường dài',
          briefing: <Moment format="HH:mm" subtract={{ hours: 2, minutes: 35 }}>{this.state.time}</Moment>,
          key: '4'
        },
      ];
      notice = "Lưu ý: Nội địa và Ngắn quốc tế có mặt trước 2h10p (trước 8 giờ có mặt trước 2h00p), đường Trung có mặt trước 2h30p và đường Dài có mặt trước 2h35p";
    } else if (this.state.base === 'cxr') {
      data = [
        {
          chang: 'Nội địa',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 30 }}>{this.state.time}</Moment>,
          key: '1'
        },
        {
          chang: 'Quốc tế',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 45 }}>{this.state.time}</Moment>,
          key: '2'
        },
      ];
      notice = "Lưu ý: Nội địa có mặt trước 1h30p và quốc tế có mặt trước 1h45p";
    } else {
      data = [
        {
          chang: 'Nội địa',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 10 }}>{this.state.time}</Moment>,
          key: '1'
        },
        {
          chang: 'Quốc tế',
          briefing: <Moment format="HH:mm" subtract={{ hours: 1, minutes: 20 }}>{this.state.time}</Moment>,
          key: '2'
        },
      ];
      notice = "Lưu ý: Nội địa có mặt trước 1h10p và quốc tế có mặt trước 1h20p";
    }

    return <div>
      <Layout>
        <div style={{marginBottom: 30 + 'px'}}>
          <RadioGroup onChange={(value) => this.setState({base: value})} horizontal value={this.state.base}>
            <RadioButton value="han" pointColor='#639'><div style={{textAlign: 'center'}}>HAN</div></RadioButton>
            <RadioButton value="sgn" pointColor='#639'><div style={{textAlign: 'center'}}>SGN</div></RadioButton>
            <RadioButton value="cxr" pointColor='#639'><div style={{textAlign: 'center'}}>CXR</div></RadioButton>
            <RadioButton value="dad" pointColor='#639'><div style={{textAlign: 'center'}}>DAD</div></RadioButton>
          </RadioGroup>

          <h3 style={{ marginTop: 30 + 'px' }}><span role="img" aria-label="clock">⏰</span> Chọn giờ bay (UTC)</h3>
          <form style={this.styles.container} noValidate>
            <TextField
              id="time"
              type="time"
              defaultValue={moment().format('HH:mm')}
              InputLabelProps={{
                shrink: true,
              }}
              inputProps={{
                step: 300, // 5 min
              }}
              style={this.styles.textField}
              //onChange={(e) => console.log(moment(e.target.value, "HH:mm").format('YYYY-MM-DD HH:mm:ss'))}
              onChange={(e) => this.setState({ time: moment(e.target.value, "HH:mm").add(7, "hours").format('YYYY-MM-DD HH:mm:ss') })}
            />
          </form>
        </div>

        <h2 style={{ textAlign: 'center', fontWeight: 'normal', color: '#9bb0c2' }}><span style={{ color: '#639', fontWeight: 'bold'}}><Moment format="HH:mm A">{this.state.time}</Moment></span> (GMT +7)</h2>

        <Table columns={columns} data={data} />
        
        <p style={{ fontSize: 12 + 'px', fontStyle: 'italic', color: '#9BB0C2' }}>{ notice }</p>

        <p style={{ fontSize: 12 + 'px', fontStyle: 'italic', textAlign: 'center' }}><span role="img" aria-label="heart">❤️</span> via Ha93</p>
      </Layout>
    </div>
  }
}


export default IndexPage
